import React, { useState, useEffect } from 'react';
import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, List, ListItem, ListItemText, Typography } from '@mui/material';

const HistoryModal = ({ open, onClose }) => {
  const [history, setHistory] = useState([]);

  useEffect(() => {
    if (open) {
      fetchHistory();
    }
  }, [open]);

  const fetchHistory = async () => {
    try {
      const response = await fetch('/api/chat/history');
      if (response.ok) {
        const data = await response.json();
        setHistory(data.history || []);
      } else {
        console.error('Error fetching chat history');
      }
    } catch (error) {
      console.error('Error fetching chat history:', error);
    }
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="md" fullWidth>
      <DialogTitle>Chat History</DialogTitle>
      <DialogContent>
        {history.length === 0 ? (
          <Typography variant="body1">No chat history available.</Typography>
        ) : (
          <List>
            {history.map((entry, index) => (
              <ListItem key={index} sx={{ borderBottom: index < history.length - 1 ? 1 : 0, borderColor: 'divider' }}>
                <ListItemText
                  primary={entry.text}
                  secondary={`${entry.sender} - ${new Date(entry.timestamp).toLocaleString()}`}
                  sx={{ 
                    bgcolor: entry.sender === 'user' ? '#e3f2fd' : (entry.sender === 'error' ? '#ffebee' : '#f5f5f5'), 
                    borderRadius: 1, 
                    p: 1 
                  }}
                />
              </ListItem>
            ))}
          </List>
        )}
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Close</Button>
      </DialogActions>
    </Dialog>
  );
};

export default HistoryModal;
