import React, { useState } from 'react';
import { BrowserRouter, Routes as RouterRoutes, Route } from 'react-router-dom';
import { ThemeProvider } from '@mui/material/styles';
import { CssBaseline } from '@mui/material';
import theme from './themes';
import routeConfigs from './routes';
import HistoryModal from './views/Modals/HistoryModal';
import ContextWindowModal from './views/Modals/ContextWindowModal';
import { Box, IconButton } from '@mui/material';
import { History as HistoryIcon, Info as InfoIcon } from '@mui/icons-material';

const App = () => {
  const [historyOpen, setHistoryOpen] = useState(false);
  const [contextOpen, setContextOpen] = useState(false);

  // Render route configurations with support for nested routes
  const renderRoutes = () => {
    return routeConfigs.map((config, index) => (
      <Route key={index} path={config.path} element={config.element}>
        {config.children && config.children.map((child, childIndex) => (
          <Route key={childIndex} path={child.path} element={child.element} />
        ))}
      </Route>
    ));
  };

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <BrowserRouter>
        <Box sx={{ position: 'relative' }}>
          <RouterRoutes>
            {renderRoutes()}
          </RouterRoutes>
          <Box sx={{ position: 'fixed', bottom: 20, right: 20, display: 'flex', flexDirection: 'column', gap: 1 }}>
            <IconButton 
              color="primary" 
              onClick={() => setHistoryOpen(true)} 
              sx={{ bgcolor: 'background.paper', boxShadow: 1 }}
              title="View Chat History"
            >
              <HistoryIcon />
            </IconButton>
            <IconButton 
              color="primary" 
              onClick={() => setContextOpen(true)} 
              sx={{ bgcolor: 'background.paper', boxShadow: 1 }}
              title="View Context Window"
            >
              <InfoIcon />
            </IconButton>
          </Box>
          <HistoryModal open={historyOpen} onClose={() => setHistoryOpen(false)} />
          <ContextWindowModal open={contextOpen} onClose={() => setContextOpen(false)} />
        </Box>
      </BrowserRouter>
    </ThemeProvider>
  );
};

export default App;
