import React from 'react';
import { Route } from 'react-router-dom';
import Loadable from '../component/Loadable';
import MainLayout from '../layout/MainLayout';

// Dashboard
const Dashboard = Loadable(() => import('../views/Dashboard/AutoBotDashboard'));

// Main Pages
const Chat = Loadable(() => import('../views/Chat'));
const Files = Loadable(() => import('../views/Files'));
const Settings = Loadable(() => import('../views/Settings'));

const MainRoutes = {
  path: '/',
  element: <MainLayout />,
  children: [
    {
      path: '/dashboard',
      element: <Dashboard />
    },
    {
      path: '/chat',
      element: <Chat />
    },
    {
      path: '/files',
      element: <Files />
    },
    {
      path: '/settings',
      element: <Settings />
    }
  ]
};

export default MainRoutes;
