import React, { useState, useEffect } from 'react';
import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, Typography, TextField } from '@mui/material';

const ContextWindowModal = ({ open, onClose }) => {
  const [contextContent, setContextContent] = useState('');
  const [tokenCount, setTokenCount] = useState(0);

  useEffect(() => {
    if (open) {
      fetchContextWindow();
    }
  }, [open]);

  const fetchContextWindow = async () => {
    try {
      const response = await fetch('/ctx_window_get');
      if (response.ok) {
        const data = await response.json();
        setContextContent(data.content || '');
        setTokenCount(data.tokens || 0);
      } else {
        console.error('Error fetching context window');
        setContextContent('Error loading context window content.');
      }
    } catch (error) {
      console.error('Error fetching context window:', error);
      setContextContent('Error loading context window content.');
    }
  };

  return (
    <Dialog open={open} onClose={onClose} maxWidth="md" fullWidth>
      <DialogTitle>Context Window</DialogTitle>
      <DialogContent>
        <Typography variant="body2" gutterBottom>
          Tokens: {tokenCount}
        </Typography>
        <TextField
          fullWidth
          multiline
          variant="outlined"
          value={contextContent}
          InputProps={{
            readOnly: true,
            style: { minHeight: '400px', fontFamily: 'monospace' }
          }}
        />
      </DialogContent>
      <DialogActions>
        <Button onClick={onClose}>Close</Button>
      </DialogActions>
    </Dialog>
  );
};

export default ContextWindowModal;
